$background-header-footer: #3A4152;
$font-white:#ffffff;
$font-black:#000000;

header {
   background: $background-header-footer;
   padding:1px;
   margin-bottom: 0%;
   nav ul li{            
      display:inline;          
      margin-left: 1em;
   }
   a{
      color:$font-white;
      text-decoration:none;
      text-transform: uppercase;
      font-size:18px;
   }   
}


#showcase{
   min-height:130px;   
   background:url('/index/assets/img/gradient.png') no-repeat;
   text-align:center;
   color: $font-white;
   .container{
      width:80%;
      margin:auto;
      overflow:hidden;
      h1{
         display: block;            
         font: 3em Arial, Helvetica,sans-serif;
         
      }
   }
  
   
}

#content-section p{
   margin-top:0em;      
   color:$font-black;    
}

#boxes{   
   margin-top:20px;
   margin-bottom:20px;
   width: 70%;
   margin-right: auto;
   margin-left: auto;
   .box{
      div h3{   
         background: pink;
         text-align: center;  
      }
      img{    
         height: 130px;
         width: 130px;
         display: block;
         margin-left: auto;
         margin-right: auto;      
      }
      p{
         text-align: justify;
      }
   }
   
}

.home{
   width: 70%;
   margin-right: auto;
   margin-left: auto;
}

#disqus_thread{
   width: 70%;
   margin-right: auto;
   margin-left: auto;
}

footer{   
   background: $background-header-footer;  

   p{
      margin-top: 3em;
      padding:0.6em;
      color:$font-white;
      text-align: center;
   }
}